import 'bulma/css/bulma.css';
import './App.scss';
import { useState } from 'react';

export const goods = [
  'Dumplings',
  'Carrot',
  'Eggs',
  'Ice cream',
  'Apple',
  'Bread',
  'Fish',
  'Honey',
  'Jam',
  'Garlic',
];

export const App = () => {
  const [item, setItem] = useState('Jam');

  return (
    <main className="section container">
      <h1 className="title is-flex is-align-items-center">
        {(item === '')
          ? 'No goods selected'
          : (
            <>
              {`${item} is selected`}

              <button
                data-cy="ClearButton"
                type="button"
                className="delete ml-3"
                onClick={() => {
                  setItem('');
                }}
              />
            </>
          )
        }
      </h1>

      <table className="table">
        <tbody>
          {goods.map((good) => {
            const isSelected = item === good;

            return (
              <tr
                data-cy="Good"
                key={good}
                className={(isSelected) && 'has-background-success-light'}
              >
                <td>
                  <button
                    data-cy={(isSelected)
                      ? 'RemoveButton'
                      : 'AddButton'}
                    type="button"
                    className={`button ${(isSelected) && 'is-info'}`}
                    onClick={
                      isSelected
                        ? () => setItem('')
                        : () => setItem(good)
                      }
                  >
                    {isSelected
                      ? `-`
                      : `+`
                    }
                  </button>

                </td>

                <td data-cy="GoodTitle" className="is-vcentered">
                  {good}
                </td>
              </tr>
            );
          })}
        </tbody>
      </table>
    </main>
  );
};
